
html, body {
width: 100%;
height: 100%;
background: #ffffff;
}
body {
    font-family: Poppins-Regular, sans-serif;
    overflow: hidden;
    margin: 0px;
    font-size:12px;
}
::-webkit-scrollbar {
    -webkit-appearance: none;
    width:15px;
    _background-color:#f0f0f0;
}
::-webkit-scrollbar-corner {
}
::-webkit-scrollbar-thumb {
    border-radius: 15px;
    border:4px solid rgba(255,255,255,0);
    background-clip:content-box;
    background-color: #e8eef1;
}
.content-wrapper.show {
    width:calc(100% - 350px);
    left:350px;
}
.content-wrapper {
    position: fixed;
    left: 50px;
    width:calc(100% - 50px);
    height:100%;
    overflow: hidden;
    -webkit-transition: left .3s, width .3s;
    transition: left .3s, width .3s;
}
#app-main {
    width:100%;
    height:100%;
}
.content-area {
    height: calc(100% -50px);
}

.v-sidebar-menu .vsm-icon {
    background-color: transparent !important;
}
.vsm-list {
    width: 100% !important;
    padding-right:0px !important;
}
.v-sidebar-menu .vsm-link {
    font-size: 12px !important;
}

.toolbar-wrapper {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex; height:49px;width:100%;border-bottom:1px solid #e0e3eb;
}
.tool {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex; -webkit-box-flex:0; -ms-flex:0 0 auto; flex:0 0 auto; -webkit-box-align:center; -ms-flex-align:center; align-items:center; -webkit-box-pack:center; -ms-flex-pack:center; justify-content:center; padding:1.5em; cursor: pointer;
}
.tool:hover {
    background-color:#f0f3fa;
}
.tool.left {
    border-right:1px solid #e0e3eb;
}
.tool.right {
    border-left:1px solid #e0e3eb;
}

































































































